* {
    box-sizing: border-box;

}

.AllUperPart div{
    justify-content: center;
    justify-items: center;



}









.ava {
    width: 400px;
    height: 350px
}

.ava img {
    overflow: hidden;
    width: 100%;
    height: 90%;
    border-radius: 5px;
}

.SM {

    margin: 5px;
}

.SM_Main {
    margin-right: 450px;
background-color: #0f3b5b08;
    border-radius: 5px;
    width:20%
}
.SM_Main:hover{
    background-color: #0f3b5b28;

}

.SM1 {
    margin-left: 12px;
}

.chMode {
    font-weight: bold;
    font-size: 18px;
    font-style: initial;
    border: blue 1px dashed;
    background: linear-gradient(top, #f9f9f9, #e3e3e3);
    text-shadow: 1px 1px #fff;
    white-space: nowrap;
    border-radius: 3px;
    content: 'Select some files';
    display: inline-block;
    background: linear-gradient(top, #f9f9f9, #e3e3e3);
    outline: none;
    font-weight: 700;



}

.uploadFile{
    color: transparent;
}

.uperFoled {
    font-weight: bold;
    padding: 5px;
    background-color: #0f2bdb06;
    width: 20%;
    border-radius: 10px;

}
.uperFoled:hover{
    background-color: #0f2bdb16;

}
.status{
    float: left;
    font-weight: bold;
    font-size: 16px;
}
.chMode:hover {
    background-color: #0f2bdb20;
}